.location-container {
    display: flex;
    flex-direction: column;
    transition: box-shadow 0.2s ease;
    border-radius: 10px;
    padding: 25px;
  }
  .location-container:hover {
    background: rgba( 0, 0, 0, 0.2);
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    padding: 25px;
  
  }
  
  .location {
    display: flex;
    flex-direction: column;
    background-color: #ffffff;
  }

  .imag-leng{
    width: 350px;
    height: 250px;

  }
  
  .hex-code {
    font-size: 12px;
    border-radius: 10px;
    padding: 10px;
    color: #fff;
    transition: background-color 0.5s ease;
  }

  .del {
    display: flex;
    text-align: center;
    height: 34px;
    width: 34px;
    justify-content: center;
    align-items: center;
    border: 1px solid #0000005e;
    padding: 5px;
    margin: 5px;
    border-radius: 5px;
    transition: background-color 0.2s ease;
  }
  
  .del:hover {
    cursor: pointer;
    background-color: #35216448;
  }