body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  background: rgb(154, 194, 235);
  background: linear-gradient(0deg, rgb(172, 182, 150) 0%, rgb(163, 154, 202) 87%);
  background-repeat: no-repeat;
  background-attachment: fixed;
}

.locations-free{
    font-size: 1.5em;
    padding:20px 0px 10px 1600px;
    font-weight: 500;
    color: black;

}

.create-location-link{
    display: flex;
    flex-direction: column;
    align-items: center;
    -webkit-backdrop-filter: blur( 3.5px);
            backdrop-filter: blur( 3.5px);
    border-radius: 10px;
    padding: 0 180px;
    max-width: 500px;
    position: absolute;
}

.create-location-icon{
    color: #02021d;

}

.box-tittle{
    font-size: 25px;
    min-width: 165px;
    width: auto;
    justify-content: center;
    letter-spacing: 0.5px;
    line-height: 50px;
    display: flex;
    height: 50px;
    padding: 30px 35px 100px 35px;
    margin-top: 0px;
    border-radius: 10px;
}
.location-container {
    display: flex;
    flex-direction: column;
    transition: box-shadow 0.2s ease;
    border-radius: 10px;
    padding: 25px;
  }
  .location-container:hover {
    background: rgba( 0, 0, 0, 0.2);
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    padding: 25px;
  
  }
  
  .location {
    display: flex;
    flex-direction: column;
    background-color: #ffffff;
  }

  .imag-leng{
    width: 350px;
    height: 250px;

  }
  
  .hex-code {
    font-size: 12px;
    border-radius: 10px;
    padding: 10px;
    color: #fff;
    transition: background-color 0.5s ease;
  }

  .del {
    display: flex;
    text-align: center;
    height: 34px;
    width: 34px;
    justify-content: center;
    align-items: center;
    border: 1px solid #0000005e;
    padding: 5px;
    margin: 5px;
    border-radius: 5px;
    transition: background-color 0.2s ease;
  }
  
  .del:hover {
    cursor: pointer;
    background-color: #35216448;
  }
.grid {
    width: 85vw;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-gap: 20px;
    grid-column-gap: 25px;
    -webkit-column-gap: 25px;
            column-gap: 25px;
}

.span-two{

    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 40px;

}


.location-create-container{
    display: block;
    padding: 200px;
}
.create-location-title{
    justify-content: center;
    display: flex;
    padding: 20px 500px 50px 500px;
}
.span-fris{
    justify-content: center;
    display:flex;
    align-items: center;
    font-size: 25px;
}

.sign-in-form{
    padding: 30px 500px 30px 700px;
    flex-direction: column;
    justify-content: center;
}

.form-control{
    text-align: center;
    display: flex;
    width: auto;
    padding: 0.8rem 0.7rem;
    font-size: 1rem;
    line-height: 1.7;
    border-radius: 5px;
    margin-top: 10px;
  
}
.btn-back{
    width: auto;
    height: 50px;
    border-radius: 5px;
    letter-spacing: 0.5px;
    line-height: 50px;
    padding: 0 35px 0 35px;
    font-size: 20px;
    color: rgb(12, 4, 4);
    font-weight: bolder;
    cursor: pointer;
    display: flex;
    justify-content: center;
    margin-top: 25px;
    align-items: center;
}

.btn{
    width: auto;
    height: 50px;
    border-radius: 5px;
    letter-spacing: 0.5px;
    line-height: 50px;
    padding: 0 35px 0 35px;
    font-size: 15px;
    color: rgb(24, 18, 18);
    font-weight: bolder;
    border: none;
    cursor: pointer;
    display: flex;
    justify-content: center;
    margin-top: 25px;
    align-items: center;
}

h1{
    display: flex;
    justify-content: center;
}
